
import React, { useState } from "react";

const Contact = () => {
  const [data, setData] = useState({
    FName:"",
    Email: "",
    MobileNumber: "",
    Textarea:"",
  });
  const InputEvent = (event) => {
        const {name,value} = event.target;
        setData((preVal)=> {
          return {
            ...preVal,
          [name]: value,
          };
        });
  };
  const formSubmit = (e) => {
    e.preventDefault();
    alert(
      'Send Message To Administration SuccessFully. We Will Contact You Soon. Thank you'
      );
    
  };
  return (
    <>
    <div className="my-5">
    <h1 className="text-center">
    Contact Form
    </h1>
    <div className="container contact_div">
      <div className="row">
      <div className="col-md-6 col-10 mx-auto">
                              <form onSubmit={formSubmit}>
                          <div className="mb-3">
                            <label for="Full_Name" className="form-label">Name </label>
                            <input type="text" className="form-control" name="FName" value={data.FName} onChange={InputEvent} placeholder="Enter Your Name Here..." required />
                          </div>
                          <div className="mb-3">
                            <label for="exampleInputEmail1" className="form-label">Email address</label>
                            <input type="email" className="form-control"  name="Email" value={data.Email} onChange={InputEvent} placeholder="Enter Your Email Here..." required />
                            <div className="form-text">We'll never share your email with anyone else.</div>
                          </div>
                          <div className="mb-3">
                            <label for="mobileNumber" className="form-label">Mobile Number</label>
                            <input type="text" className="form-control"  name="MobileNumber" value={data.MobileNumber} onChange={InputEvent} placeholder="Enter Your Mobile Number Here..." required />
                          </div>
                          <div className="mb-3">
                          <label for="exampleFromControlTextareal" class="form-label">
                            Write Some Message
                          </label>
                          <textarea
                          className="form-control"
                          name="Textarea" value={data.Textarea} onChange={InputEvent} placeholder="Write Project Details Here" required />
                         
                          
                          </div>
                          
                          <div className="mb-3 form-check">
                            <input type="checkbox" className="form-check-input" />
                            <label className="form-check-label" >Check me out</label>
                          </div>
                          <button type="submit" className="btn btn-primary">Submit</button>
                        </form>
                              
      </div>

      </div>    
     </div>

    </div>
    </>
  );
};
export default Contact;