import React from "react";
import { NavLink } from "react-router-dom";
// import web from "../src/images/img2.svg";
// import cv from "../src/images/cv2.svg";
  
const Common = (props) => {
  return (
    <>
    <section id="header" className="d-flex align-items-center">
    <div className="container-fluid ">
        <div className="row">
          <div className="col-10 mx-auto">
          <div className="row">

            <div className="col-md-6 pt-5  pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column">

                <h1> {props.Name} <strong className="brand-name">{props.brand_name}</strong></h1>
                    <h2 className="my-3"> 
                    {props.decs}
                    </h2>
              <div className="mt-3">
                <NavLink to={props.btvisit1} className="btn btn-outline-primary ">
                  {props.btname1}
                </NavLink>
              </div>
              <div className="mt-3">
                <NavLink to={props.btvisit2} className="btn btn-outline-primary ">
                  {props.btname2}
                </NavLink>
              </div>
            </div>
                <div className="col-lg-6 order-1 order-lg-2 header-img">
                <img src={props.imgsrc} className="img-fluid animated" alt="Common img" />

                </div>
            </div>
          </div>
        </div>
      </div>
    </section>
    </>
  );
};
export default Common;